import "./index.scss";
import Toolbar from "./Toolbar";
import Editor from "./Editor";
import Preview from "./Preview";
import React from "react";

const App = () => {
  const placeholder = `# Seja bem vindo ao meu Editor de Markdown!

## Este  um subttulo...
### E aqui tem outras coisas legais:

Aqui temos cdigo em Javascript, \`<div></div>\`, entre 2 backticks/crases.

\`\`\`
// Abaixo, cdigo em multi-linhas:

function anotherExample(firstLine, lastLine) {
  if (firstLine == '\`\`\`' && lastLine == '\`\`\`') {
    return multiLineCode;
  }
}
\`\`\`

Voc tambm pode escrever texto em **negrito**... uau!
Ou _itlico_.
Ou... voc tambm pode usar... **_ambos!_**
E sinta-se  vontade caso ~~queira riscar qualquer coisa~~.

H tambm [links](https://github.com/rafael-lima-vasconcelos), e
> Citaes em bloco!

E, se voc tiver muito empolgado mesmo, tambm  possvel criar tabelas:

Ttulo 1 | Ttulo 2 | Ttulo 3
------------ | ------------- | -------------
Seu contedo pode | estar aqui, e ele | pode estar aqui...
E aqui. | Okay. | Acho que voc j entendeu.

- E claro, tambm temos listas.
  - Algumas so com marcadores circulares.
     - Com diferentes nveis de identao.
        - Como neste exemplo.


1. E h tambm listas numeradas.
1. Use s 1's se quiser!
1. E por ltimo, mas no menos importante, voc tambm pode inserir imagens usando links:

![The Moon](https://freenaturestock.com/wp-content/uploads/freenaturestock-1983-1024x683.jpg)
`;

  const [state, setState] = React.useState({
    editorMaximized: false,
    previewMaximized: false,
    placeholderText: placeholder,
  });

  const handleClick = (event) => {
    if (event.target.id === "editorToolbar") {
      setState((prevState) => ({
        ...prevState,
        editorMaximized: !state.editorMaximized,
        previewMaximized: false,
      }));
    } else {
      setState((prevState) => ({
        ...prevState,
        previewMaximized: !state.previewMaximized,
        editorMaximized: false,
      }));
    }
  };

  const handleEditorChange = (event) => {
    const newValue = event.target.value;
    setState((prevState) => {
      return { ...prevState, placeholderText: newValue };
    });
  };

  if (state.editorMaximized === false && state.previewMaximized === false) {
    return (
      <div className="app">
        <Toolbar
          id="editorToolbar"
          toolbarIcon="fa fa-pencil-square-o"
          text="Editor"
          expandIcon="fa fa-arrows-alt"
          handleClick={handleClick}
        />
        <Editor
          placeholderText={state.placeholderText}
          handleEditorChange={handleEditorChange}
        />
        <Toolbar
          toolbarIcon="fa fa-file-text-o"
          text="Preview"
          id="previewToolbar"
          expandIcon="fa fa-arrows-alt"
          handleClick={handleClick}
        />
        <Preview placeholderText={state.placeholderText} />
      </div>
    );
  } else if (state.editorMaximized && !state.previewMaximized) {
    return (
      <div className="app">
        <Toolbar
          toolbarIcon="fa fa-pencil-square-o"
          text="Editor"
          expandIcon="fa fa-compress"
          handleClick={handleClick}
          id="editorToolbar"
        />
        <Editor
          placeholderText={state.placeholderText}
          handleEditorChange={handleEditorChange}
          handleClick={handleClick}
          maximized={{ height: "95vh" }}
        />
      </div>
    );
  } else {
    return (
      <div className="app">
        <Toolbar
          toolbarIcon="fa fa-file-text-o"
          text="Preview"
          handleClick={handleClick}
          id="previewToolbar"
          expandIcon="fa fa-compress"
        />
        <Preview
          placeholderText={state.placeholderText}
          handleClick={handleClick}
        />
      </div>
    );
  }
};

export default App;
